  border-radius: 1px;
  border-color: rgb(233, 232, 232);
  width: 120px;
  height: 170px;
}

.grey {
  background-color: lightgrey;
}

.row {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  padding-bottom: 1px;
}

.grey_text {
  color:rgb(151, 151, 151);
}

.daypane {
  max-width: 100%;
}


